import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  // Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Table,
  // message,
  Spin
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/itemGroupingReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  // StatusTag,
  // StatusBadge,
  // TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

//const InputGroup = Input.Group;
const Panel = Collapse.Panel;
const Option = Select.Option;
//const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({ id: "items.code" }),
      dataIndex: "item_code",
      width: "15%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "qty" }),
      dataIndex: "quantity",
      width: "10%",
      align: "right",
      render: text => {
        return <NumberFormat value={parseFloat(text)} />;
      }
    }
  ];

  const dataSource = data;
  return (
    <div style={{ marginTop: "15px", marginBottom: "15px" }}>
      <strong>
        <IntlMessages id="subItemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        style={{ marginBottom: "5px", marginTop: "5px" }}
      />
    </div>
  );
};

class ItemGroupingReport extends Component {
  state = {
    filter: {
      id_item: null,
      id_location: null,
      status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "created_at",
      sort_type: "asc"
      // pagination: null
    },
    search: null,
    search_by: null,
    search_item: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getItemGroupingReports } = this.props;
    getItemGroupingReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.itemGroupingReport.error) {
      notification("error", this.props.itemGroupingReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.itemGroupingReport.success) {
      notification("success", this.props.itemGroupingReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { search_by, search } = this.state;
    let string = "";
    if (search_by) {
      string = "search_by=" + search_by + "&search=" + search;
    } else {
      string = "search=" + search;
    }
    const { getItemGroupingReports } = this.props;
    let filter = this.filterValue();

    getItemGroupingReports(1, string, filter, this.state.filter);
  };

  //handle Search By
  handleSearchBy = e => {
    this.setState({ search_by: e });
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getItemGroupingReports } = this.props;
    this.setState({
      filter: {
        id_item: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "created_at",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null, search_by: null });
    getItemGroupingReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      const { getItemsForItemGroupingReport } = this.props;
      getItemsForItemGroupingReport(value);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_item: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_item) {
      string
        ? (string += "&id_item=" + filter.id_item)
        : (string += "id_item=" + filter.id_item);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getItemGroupingReports } = this.props;
    //const { current_page } = this.props.itemGroupingReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getItemGroupingReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getItemGroupingReports } = this.props;
    this.setState({
      filter: {
        id_item: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "created_at",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null, search_by: null });
    getItemGroupingReports();
  };

  handleSearchItem = value => {
    const { getItemsForItemGroupingReport } = this.props;
    const { id_location } = this.state.filter;
    this.setState({ search_item: value });
    if (id_location) {
      getItemsForItemGroupingReport(id_location, value);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getItemGroupingReports } = this.props;
  //   const { search, filter } = this.props.itemGroupingReport;
  //   getItemGroupingReports(page, search, filter);
  // };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_parent_code",
        rowKey: "item_parent_code"
        //width: "10%",
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_parent_name",
        rowKey: "item_parent_name"
        //width: "10%",
      },
      {
        title: getLanguage({ id: "updatedDate" }),
        date: true,
        dataIndex: "created_at",
        rowKey: "created_at",
        //width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "totalItem" }),
        dataIndex: "total_item",
        rowKey: "total_item",
        //width: "15%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "updatedBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "5%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      }
    ];

    let excelColumns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        rowKey: "index_no",
        //width: "15%",
        fixed: "left"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_parent_code",
        rowKey: "item_parent_code"
        //width: "10%",
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_parent_name",
        rowKey: "item_parent_name"
        //width: "10%",
      },
      {
        title: getLanguage({ id: "updatedDate" }),
        date: true,
        dataIndex: "created_at",
        rowKey: "created_at"
      },
      {
        title: getLanguage({ id: "totalItem" }),
        dataIndex: "total_item",
        rowKey: "total_item",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "updatedBy" }),
        dataIndex: "created_by",
        rowKey: "created_by"
      },
      {
        title: getLanguage({ id: "subItemCode" }),
        dataIndex: "item_code",
        width: "15%"
      },
      {
        title: getLanguage({ id: "subItemName" }),
        dataIndex: "item_name",
        width: "30%"
      },
      {
        title: getLanguage({ id: "qty" }),
        dataIndex: "quantity",
        width: "10%",
        align: "right",
        parse: true
      }
    ];

    // Data setup
    // const { itemGroupingReport } = this.props;
    const {
      item_grouping_reports,
      //pt_detail_search_filter,
      locations,
      items,
      filter,
      filter_select,
      loading,
      loadingForeign
    } = this.props.itemGroupingReport;
    //console.log(this.props.itemGroupingReport.base_currency); //console.log(this.state);

    let dataSource = [];
    let expandedRowKeys = [];
    let excelDataSource = [];
    if (item_grouping_reports && item_grouping_reports.length > 0) {
      // for table and print's datasource
      item_grouping_reports.map(item_grouping => {
        return dataSource.push({
          ...item_grouping,
          key: item_grouping.index_no
        });
      });

      // for print's expandedRow
      Object.keys(item_grouping_reports).map((item, index) => {
        //console.log(index);
        return expandedRowKeys.push(index + 1);
      });
      //console.log(expandedRowKeys);
      // for excel export
      item_grouping_reports.map(item => {
        item.item_groups.map(data => {
          excelDataSource.push({
            ...item,
            ...data
          });
        });
      });
    }

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const fileName = getLanguage({ id: "itemGroupingReports.pageHeader" });

    // const columns = columns.map(col => {
    //   return col;
    // });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="itemGroupingReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="itemGroupingReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.item_groups)
              }
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.item_groups)
              }
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".7cm"
              scale={0.4}
              title="itemGroupingReports.pageHeader"
            />

            <ExcelWrapper
              title="itemGroupingReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={excelColumns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.item" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "id_item")}
                      //help={<IntlMessages id="required" />}
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="selectItem" />
                        ) : (
                          <IntlMessages id="noLocation" />
                        )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      value={
                        this.state.filter.id_item
                          ? this.state.filter.id_item
                          : []
                      }
                      notFoundContent={
                        loadingForeign ? (
                          <Spin size="small" />
                        ) : this.state.filter.id_location ? (
                          <Empty
                            description={
                              <IntlMessages id="items.table.noData" />
                            }
                          />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      onSearch={this.handleSearchItem}
                    >
                      {items.map((data, key) => (
                        <Option key={key} value={data.id_item}>
                          {`${data.item_code} (${data.item_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="updatedDate" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "created_at"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="created_at">
                            <IntlMessages id="updatedDate" />
                          </Option>
                          <Option key={2} value="created_by">
                            <IntlMessages id="updatedBy" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col
                        span={8}
                        style={{
                          marginTop: "24px"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                          disabled={this.state.filter.id_item ? false : true}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="noItemLocation" />} />
            ) : !filter.includes("id_item=") ? (
              <Empty description={<IntlMessages id="noItems" />} />
            ) : item_grouping_reports.length === 0 &&
              !this.props.itemGroupingReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="itemGroupingReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    defaultExpandAllRows={true}
                    expandedRowRender={record =>
                      expandedRowRender(record.item_groups)
                    }
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    itemGroupingReport: state.itemGroupingReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(ItemGroupingReport);
