const actions = {
  getItemGroupingReports: (page, search, filter, filter_select) => ({
    type: 'GET_ITEM_GROUPING_REPORTS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter,
      filter_select
    }
  }),
  getItemsForItemGroupingReport: (id_location, search) => ({
    type: "GET_ITEMS_FOR_ITEM_GROUPING_REPORT_REQUEST",
    payload: { id_location, search }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
